import { useRef, useState, useEffect } from "react";
import { Navigate } from "react-router-dom";
import Loader from "react-loaders";
import AnimatedLetters from "../AnimatedLetters";
import "./index.scss";
import "react-toastify/dist/ReactToastify.css";
import useAuth from "../../Hooks/useAuth";
import axios from "axios";
import { useLocation, useNavigate } from "react-router-dom";

const Login = () => {
  const { setAuth } = useAuth();
  const userRef = useRef();
  const errRef = useRef();
  const [user, setUser] = useState("");
  const [pwd, setPwd] = useState("");
  const [errMsg, setErrMsg] = useState("");
  const [letterClass, setLetterClass] = useState("text-animate");
  const [redirect, setRedirect] = useState(false);
  const loginArray = "Sign in".split("");
  const navigate = useNavigate();
  const location = useLocation();

  const from = location.state?.from?.pathname || "/";
  useEffect(() => {
    return setTimeout(() => {
      setLetterClass("text-animate-hover");
    }, 3000);
  }, []);
  useEffect(() => {
    userRef.current.focus();
  }, []);

  useEffect(() => {
    setErrMsg("");
  }, [user, pwd]);

  const Submit = async e => {
    e.preventDefault();

    const headers = {
      headers: {
        "Access-Control-Allow-Origin": "*",
        Accept: "text/plain",
        "Content-Type": "application/json",
      },
    };
    const data = { usermail: user, password: pwd };
    //https://localhost:7061/api/Api/Porfels
    axios
      .post("https://anvar.nantech.az/api/login", data, {
        headers: headers,
      })
      .then(function (res) {
        console.log("sss");
        if (res.data != null) {
          var localDatas = localStorage.getItem("refreshToken");
          if (!localDatas) {
            localStorage.setItem("refreshToken", res?.data);
          }
          const accsessToken = { accsessToken: res?.data };
          setRedirect(true);
          setAuth({ accsessToken });
          navigate(from, { replace: true });
        } else {
          setRedirect(false);
          setAuth({});
          navigate(from, { replace: false });
        }
      });
  };
  if (redirect) {
    return <Navigate to="/" />;
  }
  return (
    <>
      <section className="container contact-page">
        <div className="addport">
          <h1>
            {" "}
            <AnimatedLetters
              letterClass={letterClass}
              strArray={loginArray}
              idx={15}
            />{" "}
          </h1>

          <div className="contact-form">
            <p
              ref={errRef}
              className={errMsg ? "errmsg" : "offscreen"}
              aria-live="assertive"
            >
              {errMsg}
            </p>
            <form onSubmit={Submit}>
              <ul>
                <li className="half">
                  <input
                    type="text"
                    id="username"
                    ref={userRef}
                    autoComplete="off"
                    onChange={e => setUser(e.target.value)}
                    value={user}
                    required
                    placeholder="Email"
                  />
                  {/* <input placeholder="Protfel Name" type="text" name="name" required /> */}
                </li>{" "}
                <li className="half">
                  <input
                    className="pass"
                    type="password"
                    id="password"
                    onChange={e => setPwd(e.target.value)}
                    value={pwd}
                    placeholder="Password"
                    required
                  />
                </li>{" "}
                <li>
                  <input
                    type="submit"
                    className="flat-button"
                    value="Sign in"
                  />
                </li>{" "}
              </ul>{" "}
            </form>{" "}
          </div>
        </div>
      </section>

      <Loader type="pacman" />
    </>
  );
};

export default Login;
