import { useEffect, useState } from "react";
import Loader from "react-loaders";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import { useRef } from "react";
import emailjs from "@emailjs/browser";
import AnimatedLetters from "../AnimatedLetters";
import "./index.scss";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Contact = () => {
  const [letterClass, setLetterClass] = useState("text-animate");
  const form = useRef();
  const contactArray = "Contact Me".split("");

  useEffect(() => {
    return setTimeout(() => {
      setLetterClass("text-animate-hover");
    }, 3000);
  }, []);

  const sendEmail = e => {
    e.preventDefault();

    // emailjs.sendForm(
    //     process.env.REACT_APP_EMIAL_SERVICE,
    //     process.env.REACT_APP_TEMPLATE_ID,
    //     form.current,
    //     process.env.REACT_APP_PUBLIC_KEY
    //   )

    emailjs
      .sendForm(
        "service_anb0jks",
        "template_vgc02rs",
        form.current,
        "BQoneIvyHVhU-5Nly"
      )
      .then(
        () => {
          // alert('Message successfully sent!')
          toast.success("Message successfully sent!", {
            position: "bottom-center",
            autoClose: 3500,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
          });
          const timeout = setTimeout(() => {
            window.location.reload(false);
          }, 3900);

          return () => clearTimeout(timeout);
        },
        () => {
          // alert('Failed to send the message, please try again')
          toast.error("Failed to send the message, please try again", {
            position: "bottom-center",
            autoClose: 3500,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
          });
        }
      );
  };

  return (
    <>
      <div className="container contact-page">
        <div className="text-zone contactme">
          <h1>
            <AnimatedLetters
              letterClass={letterClass}
              strArray={contactArray}
              idx={15}
            />{" "}
          </h1>
          <div className="contact-form">
            <form ref={form} onSubmit={sendEmail}>
              <ul>
                <li className="half">
                  <input placeholder="Name" type="text" name="name" required />
                </li>{" "}
                <li className="half">
                  <input
                    placeholder="Email"
                    type="email"
                    name="email"
                    required
                  />
                </li>{" "}
                <li className="half">
                  <input
                    placeholder="Subject"
                    type="text"
                    name="subject"
                    required
                  />
                </li>{" "}
                <li className="half">
                  <textarea
                    placeholder="Message"
                    name="message"
                    required
                  ></textarea>{" "}
                </li>{" "}
                <li>
                  <input type="submit" className="flat-button" value="SEND" />
                </li>{" "}
              </ul>{" "}
              <ToastContainer />
            </form>{" "}
          </div>{" "}
        </div>{" "}
        <div className="info-map">
          Aff busines house
          <br />
          Nesimi, <br />
          Baku, <br />
          Azerbaijan <br />
        </div>{" "}
        <div className="map-wrap">
          <MapContainer center={[40.3771981, 49.8517268]} zoom={13}>
            <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
            <Marker position={[40.3771981, 49.8517268]}>
              <Popup>
                {" "}
                Sudip lives here, come over for a cup of coffee: ){" "}
              </Popup>{" "}
            </Marker>{" "}
          </MapContainer>{" "}
        </div>{" "}
      </div>{" "}
      <Loader type="pacman" />
    </>
  );
};

export default Contact;
