import { Route, Routes } from "react-router-dom";
import Home from "./components/Home";
import About from "./components/About";
import Contact from "./components/Contact";
import Layout from "./components/Layout";
import Skills from "./components/Skills";
import Addport from "./components/Addport";
import Soundbar from "./components/Soundbar";
import Login from "./components/Login";
import Acces from "./components/Acces";
import axios from "axios";
import "./App.scss";
import useAuth from "./Hooks/useAuth";
import { useEffect, useState } from "react";
import Portfolio from "./components/Portfolio";
//import { useLocation, useNavigate } from "react-router-dom";
import RequiredAuth from "./components/RequiredAuth/RequiredAuth";

function App() {
  const { setAuth } = useAuth();
  const [setRedirect] = useState(true);

  const headers = {
    headers: {
      Accept: "application/json",
      withCredentials: true,
      "Content-Type": "application/json",
    },
  };

  useEffect(() => {
    const data = { toke: localStorage.getItem("refreshToken") };
    (async () => {
      axios
        .post("https://anvar.nantech.az/api/refresh-token", data, {
          headers: headers,
        })
        .then(function (res) {
          if (res.data != null) {
            const accsessToken = res?.data;
            localStorage.setItem("refreshToken", res?.data);
            setAuth({ accsessToken });
          } else {
            setRedirect(false);
            localStorage.clear();
            setAuth({});
          }
        });
    })();
  }, []);

  console.log("asss");
  return (
    <>
      <Soundbar />
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route index element={<Home />} />{" "}
          <Route path="about" element={<About />} />{" "}
          <Route path="portfolio" element={<Portfolio />} />{" "}
          <Route path="/skills" element={<Skills />} />{" "}
          <Route path="/contact" element={<Contact />} />{" "}
          <Route element={<RequiredAuth />}>
            <Route path="/addport" element={<Addport />} />
            {/* <Route path="/logout" /> */}
          </Route>
          <Route path="/Ad-Login" element={<Login />} />{" "}
          <Route path="/Acces" element={<Acces />} />{" "}
        </Route>{" "}
      </Routes>{" "}
    </>
  );
}

export default App;
