import React, { Component } from "react";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import ReactCardCarousel from "react-card-carousel";
import axios from "axios";
import { useState } from "react";
import "./portfel.scss";

class List extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
    };
  }
  componentDidMount() {
    const config = {
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH,OPTIONS",
        mode: "cors",
      },
    };
    // const headers = {
    //   headers: {
    //     Accept: "text/plain",
    //     "Access-Control-Allow-Origin": "*",
    //     "Content-Type": "application/json",
    //   },
    // };
    // axios({
    //   method: "get",
    //   url: "https://localhost:7061/api/Porfels",
    //   responseType: "stream",
    // }).then(function (res) {
    //   console.log(res);
    // });
    console.log("did");
    axios.get("https://anvar.nantech.az/api/Portfels", config).then(res => {
      this.setState({ data: res.data });
    });
  }
  componentDidUpdate() {
    console.log("up");
  }

  render() {
    if (this.state.data != null) {
      return (
        <ReactCardCarousel autoplay={false} alignment="vertical">
          {this.state.data.map(x => {
            return (
              <div className="Card_style" key={x.aimPortfelId}>
                <Test tst={x} />
                <div className="card-info">
                  <img
                    src={"https://anvar.nantech.az/" + x.aimPortfelPhoto}
                    alt=""
                  />
                </div>
              </div>
            );
          })}
        </ReactCardCarousel>
      );
    } else {
      return (
        <>
          <h1>Salam</h1>
        </>
      );
    }
  }
}
function Test(props) {
  const [fullscreen, setFullscreen] = useState(false);

  return (
    <div className="card_btn">
      <Button variant="" onClick={() => setFullscreen(true)}>
        Read more
      </Button>

      <Modal
        size="xxl-down"
        show={fullscreen}
        onHide={() => setFullscreen(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-modal-sizes-title-lg">
            {props.tst.aimPortfelTitle}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <iframe
            src={props.tst.aimPortfelDomen}
            title="Hello"
            style={{
              border: "none",
              width: "100%",
              height: "100%",
            }}
          />
        </Modal.Body>
      </Modal>
    </div>
  );
}
export default List;
