import { useEffect, useState } from "react";
import Loader from "react-loaders";
import { useRef } from "react";
import AnimatedLetters from "../AnimatedLetters";
import "./index.scss";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";

const Contact = () => {
  const [letterClass, setLetterClass] = useState("text-animate");
  const form = useRef();
  const contactArray = "Add Portfel".split("");
  const [title, setTitle] = useState("");
  const [domen, setDomen] = useState("");
  const [About, setAbout] = useState("");
  const [img, setImg] = useState("");

  useEffect(() => {
    return setTimeout(() => {
      setLetterClass("text-animate-hover");
    }, 3000);
  }, []);

  var config = {
    headers: {
      accept: "*/*",
      "Content-Type": "multipart/form-data",
    },
  };

  const Addport = async e => {
    e.preventDefault();

    const data = {
      p: {
        aimPortfelTitle: title,
        aimPortfelDomen: domen,
        aimPortfelAbout: About,
        toke: localStorage.getItem("refreshToken"),
      },
      firstFile: img,
    };

    console.log(data);
    axios
      .post("https://anvar.nantech.az/api/Data/addport", data, config)
      .then(res => {})
      .then(
        () => {
          toast.success("Message successfully sent!", {
            position: "bottom-center",
            autoClose: 3500,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
          });
        },
        () => {
          toast.error("Failed to send the message, please try again", {
            position: "bottom-center",
            autoClose: 3500,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
          });
        }
      );
  };

  return (
    <>
      <div className="container add-page">
        <div className="text-zone2 addport2">
          <h1>
            <AnimatedLetters
              letterClass={letterClass}
              strArray={contactArray}
              idx={15}
            />{" "}
          </h1>{" "}
          <div className="contact-form add-form">
            <form ref={form} onSubmit={Addport}>
              <ul>
                <li className="half">
                  <input
                    onChange={e => setTitle(e.target.value)}
                    placeholder="Protfel Title"
                    type="text"
                    name="aimPortfelTitle"
                    required
                  />
                </li>{" "}
                <li className="half">
                  <input
                    placeholder="Domen (https://www.example.com/)"
                    onChange={e => setDomen(e.target.value)}
                    type="text"
                    name="aimPortfelDomen"
                    required
                  />
                </li>{" "}
                <li>
                  <input
                    placeholder="Photo"
                    type="file"
                    name="firstFile"
                    onChange={event => setImg(event.target.files[0])}
                    required
                  />
                </li>{" "}
                <li className="txarea">
                  <textarea
                    placeholder="About"
                    onChange={e => setAbout(e.target.value)}
                    name="aimPortfelAbout"
                    className="abbtext"
                    required
                  ></textarea>{" "}
                </li>{" "}
                <li>
                  <input type="submit" className="flat-button" value="SEND" />
                </li>{" "}
              </ul>{" "}
              <ToastContainer />
            </form>{" "}
          </div>{" "}
        </div>{" "}
      </div>{" "}
      <Loader type="pacman" />
    </>
  );
};

export default Contact;
